<template>
    
    <section class="pb-8" id="contact">
      <v-container fluid>
        <v-row align="center" justify="center">
          <v-col cols="9">
            <v-row justify="left">
              <v-col cols="12" sm="5">
                <h1 class="font-weight-light display-1">Contate-nos</h1>
                <h2 class="font-weight-light mt-3">Porque para nós o seu contacto é importante!!!!!</h2>
                <h2 class="font-weight-light mt-3">Horário: 10:00 - 20:00 GMT</h2>
                <h2 class="font-weight-light mt-3">Localização - Portugal</h2>
                <h2 class="font-weight-light mt-3">Email - hi@digitalwish.pt</h2>
     
              </v-col>
              <v-col cols="10" sm="7">
                <form role="form" method="POST" @submit.prevent="sendEmail">                
                <!--NAME -->
                <v-col cols="6">
                  <v-text-field
                    v-model="nameMsg"
                    :rules="nameRules"
                    :counter="50"
                    label="First name"
                    required
                ></v-text-field>
                </v-col>                
                <!--EMAIL -->
                <v-col cols="6">
                  <v-text-field
                    v-model="emailMsg"
                    :rules="mailRules"
                    :counter="50"
                    label="Email"
                    required
                ></v-text-field>
                
                </v-col>
                <!--MESSAGE-->
                <v-col cols="6">
                  <v-text-field
                    v-model="messageMsg"
                    :counter="200"
                    label="Text"
                    required
                ></v-text-field> 
                </v-col>

                <!--SUBMIT BUTTON-->
                <v-col cols="1">
                <input type="submit" :disabled="!nameMsg || !emailMsg || !messageMsg"/> </v-col>
                <!--LOADING MESSAGE-->               
                </form>
                <div v-if="loadingTxt">
                <p class="mb-8 text-primary">A entregar o seu post...</p>
                <div v-if="box"></div>   
              </div>
              
            <!-- <vue-recaptcha sitekey="6LftEnggAAAAAMB5PTcjAATr9rLuYOUXs87XLa_T"></vue-recaptcha>-->
            
              </v-col>
              
            </v-row>
            
          </v-col>
          <div>
            <v-col> 
          <v-btn
                rounded
                outlined
                large
                @click="$vuetify.goTo('#home')"
                class="mt-5"
                align-center
              
              >
                Inicio
                <v-icon class="ml-4">mdi-arrow-up</v-icon>
              </v-btn>
            </v-col>
            <v-col> 
              <v-btn
                rounded
                outlined
                large
                @click="$vuetify.goTo('#about')"
                class="mt-5"
                align-center
              
              >
                Sobre
                <v-icon class="ml-4">mdi-arrow-up</v-icon>
              </v-btn>
            </v-col>
            </div>   
        </v-row>
      </v-container>
    </section>
  </template>
  
  <style scoped>
  #contact {
    background-color: #f4f7f5;
  }
  label {
    font-size: 20px;
}
   
  </style>
  
  <script>
    import { VueRecaptcha } from 'vue-recaptcha';
    import axios from 'axios';
    
    export default {
    components: { VueRecaptcha },
    
    data() {    
    return {      
          nameMsg: '',      
          emailMsg: '',      
          messageMsg: '',      
          loadingTxt: false,
          box:false,
          };  
    },
    
    methods: {    
    sendEmail() {      
    this.loadingTxt= true;  
    this.box = window.alert("Mesagem enviada com sucesso!") ;    
    axios.post('https://formspree.io/f/xwkzlzwr',{
       name: this.nameMsg,          
       from: this.emailMsg,          
       _subject: `${this.nameMsg} | Friendly Message from Digitalwish Page`,
       message: this.messageMsg,},
    ).then((response) => {
       this.nameMsg = '';
       this.emailMsg = '';
       this.messageMsg = '';
       this.loadingTxt = false;  
       this.$router.push({ path: '/success' }); 
         
    })
    },
  },
  
}
    </script>